* {
  box-sizing: border-box;
}

body {
  margin-top: 75px;
  font-family: sans-serif;
}

.main-carousel {
  margin: 20px;
}

.carousel-cell {
  margin-right: 20px;
  overflow: hidden;
}

.carousel-cell img {
  display: block;
  height: 200px;
}

@media screen and (min-width: 768px) {
  .carousel-cell img {
    height: 400px;
  }
}

.video-container {
  height: 350px;
  width: 550px;
  overflow: hidden;
  position: relative;
}

.video-container iframe {
  width: 100%;
  height: 100%;
}

/* Section 2 */
.message {
  background-color: #31323a;
  margin-top: 50px;
  padding: 40px 110px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 140px;
}

.message-first-half {
  grid-column: 1;
}

.message-latter-half {
  grid-column: 2;
}

.message h2 {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: normal;
  line-height: 1.3;
}

.message-first-half p {
  color: #adadad;
  margin-top: 32px;
  line-height: 1.6;
  font-size: 1.13rem;
}

.message-first-half p:nth-child(2) {
  margin-top: 16px;
}

.message-latter-half p {
  color: #adadad;
  margin-top: 13px;
  line-height: 1.6;
  font-size: 1.13rem;
}

.message-latter-half p::first-word {
  margin-right: 140px; /* Adjust the value as needed */
}

.message-emphasis {
  font-weight: bold;
}

/* Section3 */

.Project {
  overflow: hidden;
}

.Project-Image {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ProjectItem {
  margin: 30px;
  padding: 10px;
}

.ProjectItem img {
  width: 400px;
  height: 600px;
  object-fit: cover;
  cursor: pointer;
  transition: transform 0.2s;
}

.ProjectItem img:hover {
  transform: scale(1.1);
}

/* LANDSCAPE */

.Projects {
  overflow: hidden;
}

.Project-Images {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ProjectItems {
  margin: 30px;
  padding: 10px;
}

.ProjectItems img {
  width: 400px;
  height: 200px;
  object-fit: cover;
  cursor: pointer;
  transition: transform 0.2s;
}

.ProjectItems img:hover {
  transform: scale(1.1);
}

/* Modal styles */
#enlarged-image-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  justify-content: center;
  align-items: center;
}

#enlarged-image-content {
  text-align: center;
}

#enlarged-image {
  max-width: 80%;
  max-height: 80vh;
  height: auto;
}

